      info: '伝統的なローグライク+育成RPGゲーム\n\n主人公は仲間たちと何度もダンジョンを探索し、ダンジョンから得たリソースを利用して自分のチームを育成する必要があります。\n\nゲームルール：\n1. 私が動けば敵も動くターン制\n2.',
      rulesInfo: '',
